.reportList{
    .el-card{
      background-color: #f1f1f1;
  }
}
.demo-table-expand {
  font-size: 0;
  th{
      background-color: #dadada;
  }
  
}
.demo-table-expand label {
  width: 100px;
  color: #409eff;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
  color: #666;
}
// .demo-table-expand .el-form-lang {
//   margin-right: 0;
//   margin-bottom: 0;
//   width: 100%;
// }
</style>

<template>
  <div class="app-container reportList">
    <div class="filter-container">
      <el-input
        placeholder="用户名"
        v-model="listQuery.username"
        style="width: 200px;"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-input
        placeholder="真实姓名"
        v-model="listQuery.name"
        style="width: 200px;"
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-button class="filter-item" type="primary" icon="el-icon-search" @click="searchData">搜索</el-button>
      <el-button
        class="filter-item"
        style="margin-left: 10px;"
        type="primary"
        icon="el-icon-edit"
        @click="addData()"
      >添加</el-button>
    </div>
    <el-table
      v-loading="listLoading"
      :data="data"
      border
      fit
      highlight-current-row
      style="width: 100%;"
    >
      <el-table-column type="expand">
        <template slot-scope="props">
        <el-card>
            <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item class="el-form-lang" label="报告名称">
              <span>{{ props.row.reportName }}</span>
            </el-form-item>
            <el-form-item label="缺陷总数">
              <span>{{ props.row.total }}</span>
            </el-form-item>
            <el-form-item label="已关闭缺陷数">
              <span>{{ props.row.closed }}</span>
            </el-form-item>
            <el-form-item label="残留缺陷数">
              <span>{{ props.row.remain }}</span>
            </el-form-item>
            <el-form-item label="解决率">
              <span>{{ props.row.probability }}%</span>
            </el-form-item>
            <el-form-item class="el-form-lang" label="新增功能">
              <span>{{ props.row.new }}</span>
            </el-form-item>
            <el-form-item class="el-form-lang" label="修改模块">
              <span>{{ props.row.change }}</span>
            </el-form-item>
            <el-form-item class="el-form-lang" label="重点测试">
              <span>{{ props.row.important }}</span>
            </el-form-item>
            <el-table :data="props.row.bugList" border style="width: 100%;margin-top:10px;">
              <el-table-column align="center" prop="description" label="BUG描述"></el-table-column>
              <el-table-column align="center" prop="reason" label="产生原因"></el-table-column>
              <el-table-column align="center" prop="information" label="修复情况"></el-table-column>
            </el-table>
          </el-form>
        </el-card>
          
        </template>
      </el-table-column>
      <el-table-column label="报告名称" align="center" prop="reportName"/>
      <el-table-column label="发布人" align="center" prop="name"/>
      <el-table-column label="审核人" align="center" prop="checkName"/>
      <el-table-column label="发布时间" align="center" prop="createTime">
        <template slot-scope="scope">
          <span>{{ scope.row.createTime | parseTime('{y}-{m}-{d} {h}:{m}:{s}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="审核时间" align="center" prop="checkTime">
        <template slot-scope="scope">
          <span>{{ scope.row.checkTime | parseTime('{y}-{m}-{d} {h}:{m}:{s}') }}</span>
        </template>
      </el-table-column>
      <el-table-column label="审核状态" align="center">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.status==1" type="success">已通过</el-tag>
          <el-tag v-if="scope.row.status==0" type="info">待审核</el-tag>
          <el-tag v-if="scope.row.status==2" type="warning">未通过</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="250" class-name="small-padding fixed-width">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="mini"
            :disabled="scope.row.status!=0?true:false"
            @click="pass(scope.row.id,'1')"
          >通过</el-button>
          <el-button
            type="warning"
            size="mini"
            :disabled="scope.row.status!=0?true:false"
            @click="pass(scope.row.id,'2')"
          >不通过</el-button>
          <el-button
            type="info"
            size="mini"
            :disabled="scope.row.userId!=userId"
            @click="reset(scope.row.id)"
          >重发</el-button>
        </template>
      </el-table-column>
    </el-table>

    <pagination
      v-show="total>0"
      :total="total"
      :page.sync="listQuery.page"
      :limit.sync="listQuery.limit"
      @pagination="getData"
    />
  </div>
</template>

<script>
import { Report } from "@/api/report";
import Pagination from "@/components/Pagination"; // Secondary package based on el-pagination
import { mapGetters } from "vuex";
export default {
  components: { Pagination },
  computed: {
    ...mapGetters(["name", "userId"])
  },
  data() {
    return {
      dialogFormVisible: false,
      listQuery: {
        name: null,
        username: null,
        page: 1,
        limit: 10
      },
      listLoading: false,
      total: 0,
      // 需要新增和编辑的数据对象
      data: []
    };
  },
  created() {
    // this.getDepartment();
    this.getData();
  },
  methods: {
    /**
     * @description 搜索数据
     * @function searchData
     * @return {type} {description}
     */
    searchData() {
      this.listQuery.page = 1;
      this.getData();
    },
    /**
     * @description 删除部门子部门
     * @function {function name}
     * @return {type} {description}
     */
    deleteChildren(children) {
      for (let iterator of children) {
        if (iterator.children.length > 0) {
          this.deleteChildren(iterator.children);
        } else {
          delete iterator.children;
        }
      }
    },
    /**
     * @description 获取数据
     * @function getData
     * @return {type} {description}
     */
    getData() {
      Report.get(this.listQuery).then(res => {
        this.data = res.data;
        this.total = res.total;
      });
    },
    /**
     * @description 提交数据
     * @function {function name}
     * @return {type} {description}
     */
    reset(id) {
      this.$router.push({
        path: "/report/create",
        query: {
          id: id
        }
      });
    },

    pass(id,status) {
      this.$confirm("确定执行该操作?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {

        Report.update({
            name:this.name,
            id,
            status: status
          }).then(res => {
            this.$message({
              type: "success",
              message: "操作成功"
            });
            this.getData();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};
</script>
